}
.cookies--closed {
  animation: cookiesClosedMobile 0.8s forwards;
}

@keyframes cookiesOpenMobile {
  from {
    bottom: 0;
    transform: translateY(100%);
  }
  to {
    bottom: 40px;
    transform: translateY(0);
  }
}
@keyframes cookiesOpenDesctop {
  from {
    bottom: 0;
    transform: translateY(100%);
  }
  to {
    bottom: 30px;
    transform: translateY(0);
  }
}
@keyframes cookiesClosedMobile {
  from {
    bottom: 40px;
    transform: translateY(0);
  }
  to {
    bottom: 0;
    transform: translateY(100%);
  }
}
@keyframes cookiesClosedDesctop {
  from {
    bottom: 30px;
    transform: translateY(0);
  }
  to {
    bottom: 0;
    transform: translateY(100%);
  }
}
@media (min-width: 1200px) {
