.header__bottom-box {
  display: flex;
  justify-content: space-between;
}
.header__logo {
  display: flex;
  gap: 7px;
}
.header__logo-text {
  font-weight: 600;
  font-size: 29px;
}
.header__button-contact, .header__menu {
  display: none;
}
.header__menu-open {
  padding: 1px 4px;
}
.header__menu--open {
  position: fixed;
  top: 0;
  left: 0;
  padding: 196px 16px 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #000;
  z-index: 1;
}
.header__menu-closed {
  position: absolute;
  top: 15px;
  right: 15px;
}
.header__nav {
  display: flex;
  flex-direction: column;
  gap: 178px;
}

@media (min-width: 1200px) {
  .header__top {
    padding-left: 61px;
  }
  .header__bottom-box {
    align-items: center;
  }
  .header__bottom {
    margin: 7px auto 0;
    max-width: 1360px;
  }
  .header__logo {
    margin-left: 7px;
  }
  .header__logo svg {
    width: 35px;
    height: 40px;
  }
  .header__logo-text {
    font-size: 36px;
  }
  .header__menu-open {
    display: none;
  }
  .header__button-contact {
    display: block;
  }
}
.info {
  display: grid;
  gap: 60px;
}
.info__item {
  display: grid;
  gap: 13px;
}
.info__subtitle {
  font-size: 32px;
  line-height: 34px;
  font-weight: 700;
}
.info__text {
  line-height: 20px;
}
.info__svg--1 {
  margin: 10px 0 10px 3px;
}
.info__svg--2 {
  margin: 3px 13px 3px -7px;
}
.info__svg--3 {
  margin: 25px 0;
}
.info__svg--4 {
  margin: 24px 0;
}

@media (min-width: 600px) {
  .info {
    grid-template-columns: 1fr 1fr;
    align-items: start;
  }
}
@media (min-width: 768px) {
  .info__subtitle {
    font-size: 36px;
    line-height: 38px;
  }
  .info__text {
    font-size: 24px;
    line-height: 29px;
    margin-top: 14px;
  }
}
@media (min-width: 1200px) {
  .info {
    gap: 72px 56px;
  }
  .info__item {
    max-width: 335px;
    gap: 7px;
  }
  .info__subtitle {
    font-size: 48px;
    line-height: 49px;
  }
  .info__svg--4, .info__svg--3 {
    margin: 25px 0 30px;
  }
}
.nav__list {
  display: grid;
  gap: 18px;
}
.nav__link {
  font-size: 20px;
}

.promo {
  margin-top: 81px;
  text-align: center;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: center;
  gap: 90px;
  flex-direction: column;
  padding: 58px 0;
  align-items: center;
}
.promo__img {
  position: absolute;
}
.promo__animation {
  position: relative;
  display: flex;
  overflow: hidden;
  user-select: none;
  gap: 20px;
  font-size: 48px;
}
.promo__text {
  flex-shrink: 0;
  display: flex;
  gap: 20px;
  counter-reset: item;
  justify-content: space-around;
  min-width: 100%;
  animation: scroll 20s linear infinite;
}
.promo__text--reverse {
  animation-direction: reverse;
}
.promo__text-green {
  font-weight: 700;
  color: #2FDBBC;
}
.promo__text-violet {
  font-weight: 700;
  color: #A64CED;
}
.promo__text-orange {
  font-weight: 700;
  color: #FFAA4C;
}
.promo__text-outside {
  font-weight: 700;
  color: #181818;
  text-shadow: 0px 0px #444;
}

@media (min-width: 1200px) {
  .promo {
    margin-top: 116px;
    gap: 58px;
  }
  .promo__img img {
    width: 308px;
    height: 623px;
  }
  .promo__animation {
    font-size: 128px;
  }
}
@keyframes scroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc(-100% - 20px));
  }
}
.start {
  padding: 40px 16px;
  margin-top: 80px;
}
.start__container {
  padding: 39px 20px;
  display: grid;
  gap: 39px;
  border: 1px solid #CFCFCF;
  border-radius: 60px;
}
.start__title {
  font-size: 40px;
  line-height: 43px;
  font-weight: 700;
  color: #000;
  text-align: center;
}

@media (min-width: 768px) {
  .start__container {
    padding: 60px 20px;
    gap: 39px;
    justify-content: center;
  }
  .start__title {
    font-size: 64px;
    line-height: 68px;
  }
  .start__button {
    width: fit-content;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .start {
    padding: 100px 0;
    margin-top: 100px;
  }
  .start__container {
    max-width: 1320px;
    margin: 0 auto;
  }
}
.statistics {
  padding: 38px 16px;
  margin-top: 79px;
  background-color: #954CED;
}
.statistics__list {
  display: flex;
  flex-direction: column;
  gap: 36px;
}
.statistics__item {
  display: flex;
  align-items: center;
  gap: 15px;
}
.statistics__number {
  font-size: 40px;
  font-weight: 700;
}
.statistics__text {
  font-size: 20px;
  font-weight: 600;
}

@media (min-width: 768px) {
  .statistics {
    margin-top: 82px;
  }
  .statistics__list {
    flex-direction: row;
    margin: 0 auto;
  }
  .statistics__item {
    flex-direction: column;
  }
  .statistics__number {
    font-size: 64px;
  }
  .statistics__text {
    font-size: 24px;
  }
}
@media (min-width: 1200px) {
  .statistics {
    display: flex;
  }
  .statistics__list {
    margin: 0 auto;
    gap: 97px;
  }
  .statistics__item {
    align-items: flex-start;
  }
  .statistics__item:nth-child(1) {
    max-width: 220px;
  }
  .statistics__item:nth-child(2) {
    max-width: 165px;
  }
  .statistics__item:nth-child(3) {
    max-width: 163px;
  }
}